body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.main-container {
  background: -webkit-linear-gradient(315deg, #83a4d4 0%, #b6fbff 100%);
  background: linear-gradient(135deg, #83a4d4 0%, #b6fbff 100%);
  height: 100vh;
}
.bit-container {
  margin: 0px auto;
  height: 100%;
  width: 100%;
  max-width: 800px;
}

.bit-header {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
}

.bit-section {
  margin-bottom: 20px;
}

.bit-list {
  margin: 10px 0px;
}
.bit-list::before {
  top: 4px;
}

#ff::before {
  top: 4px;
}

.footer {
  height: 40px;
  display: flex;
  align-items: center;
}

.bit-footer-icon {
  margin-left: 10px;
}


@media only screen and (max-width: 600px) {
  .bit-header {
    font-size: 24px;
  }

  .footer {
    display: block;
    margin: 10px;
  }
}
